import React, { useState, useEffect } from 'react';
import axios from 'axios';

import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import MailIcon from '@mui/icons-material/Mail';
import List from '@mui/material/List';

function SideList() {
  const [shop, setShop] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  useEffect(() => {
    console.log('hi')
    const fetchUsers = async () => {
      try {
        setError(null);
        setShop(null);
        setLoading(true);
        const response = await axios.get(
          'http://sulrae.com/api/shop'
        );
        setShop(response.data);
      } catch (e) {
        setError(e);
      }
      setLoading(false);
    };

    fetchUsers();
  },[]);
  

  if (loading) return <div>Î°úÎî©Ï§ë..</div>;
  if (error) return <div>ÏóêÎü¨Í∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§</div>;
  if (!shop) return null;
  return (
      <>
        {shop.map(shop => (
            <ListItem  button key={shop.id}>
              <List>
                <img className="phoneImage" alt={shop.id} src={shop.image1} width="150px" />
                <div>
                  {shop.name} ÌèâÏ†ê: {shop.score} <br/>
                  {shop.address} <br/>
                  ÌèâÏùº : {shop.open_time_weekday} <br/>
                  Ï£ºÎßê : {shop.open_time_weekend} <br/>
                  {shop.tel}
                </div>
              </List>
              </ListItem>
        ))}
      </>
  );
}

export default SideList;